const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
   let check = loginService.checkIdentity();
   let meta_admin = to.matched.some(record=>record.meta.admin);
   let meta_log = to.matched.some(record=>record.meta.log);
   let meta_auth = to.matched.some(record=>record.meta.auth);
  
   if (to.path === '/login' )  return next()  

   if(check && meta_log){
      let admin = loginService.isAdmin();
      let ath = loginService.getAth();
      if (to.path === '/auth' && admin)  return next();
      if(meta_admin){
          if(admin){
           if(meta_auth){
              if(ath.auth=='valido'){
                next();
              }else{
                next('/auth')
              }
           }else{ // rutas de colaboradores 
              if(ath.auth=='valido'){
                next();
              }else{
                next('/auth')
              }
           }
          }else{
            let identity = loginService.getIdentity();
            next(`${identity.city}/${identity.depto}/${identity.num}`) // es colaborador
          }
      }else{ //rutas especificas de colaborador
        if(admin){ // si la ruta no requiere admin pero si es admin que verifique que este autentificado
          if(ath.auth=='valido'){ 
            next();
          }else{
            next('/auth')
          }
         }else{
            next();
         }
      }

   }else{
     next('/login')
   }

});

export default router
