import axios from "axios";
import moment from 'moment';

const jwt = require('jwt-simple');
const clave = 'fanosa-app2021-0x';
const ENDPOINT_PATH = "http://localhost:3000/api/";
let userToken = "";
let ath ="";

export default {
  login(email, password, gettoken = null) {
    if (gettoken != null) {
      gettoken = "true";
    }
    const user = {
      email,
      password,
      gettoken,
    };

    let headers = { "Content-Type": "application/x-www-form-urlencoded" };
    return axios.post(ENDPOINT_PATH + "login", user, headers);
  },

  // guardar token en LS
  setToken(token) {
    localStorage.setItem("token", token);
  },

   //Obtener el token guardado en el LS
  getToken() {
    let token = localStorage.getItem("token");

    if (token && token != "undefined") {
      userToken = token;
    } else {
      userToken = null;
    }

    return userToken;
  },

  // decodificar token
  decode(token){
    let decoded;
    let user = {};
    if(token != null){
     decoded = jwt.decode(token, clave);
     user  = decoded;
    }

    return user;
  },

  logout() {
        localStorage.removeItem('token');

        //this.identity = null;
        //this.token = null;

        //Redireccion a inicio
        this.$router.push("/login");
  },

  createAuth(){
     var payload ={
         auth: 'valido',
         iat:  moment().format(), 
         exp: moment().add(30, 'days').format()  
     };
     let token = jwt.encode(payload, clave);
     return  token;
  },

  setAth(ath) {
    localStorage.setItem("ath", ath);
  }, 

  getAth() {
    let token = localStorage.getItem("ath");

    if (token && token != "undefined") {
      ath = token;
    } else {
      ath = null;
    }

    return ath;
  },


};

